"use client";

import React from "react";
import { X } from "lucide-react";

interface ReceiptItem {
    id: number;
    name: string;
    price: number;
    quantity: number;
    seller: string;
    sellerVerified: boolean;
    image?: string;
}

interface OrderReceiptModalProps {
    isOpen: boolean;
    onClose: () => void;
}

const OrderReceiptModal: React.FC<OrderReceiptModalProps> = ({
    isOpen,
    onClose,
}) => {
    if (!isOpen) return null;

    const orderData = {
        orderId: "#024588",
        orderTime: "Wednesday 1st January 2026",
        itemsTotal: 48000.0,
        shipping: 8000.0,
        orderTotal: 56000.0,
        shippingAddress: {
            name: "Otor John Stephen",
            phone: "+234 8134756623",
            address:
                "38 Asheik Jarma Street, Abuja, Federal Capital Territory\nNigeria",
        },
        paymentMethod: {
            type: "Master card",
            last4: "XXXX 9682",
            paidOn: "Wednesday 1st January 2026",
            amount: 56000.0,
        },
    };

    const items: ReceiptItem[] = [
        {
            id: 1,
            name: "The name of the product",
            price: 50000.0,
            quantity: 2,
            seller: "Offizy Stores",
            sellerVerified: true,
        },
        {
            id: 2,
            name: "The name of the product",
            price: 50000.0,
            quantity: 2,
            seller: "Offizy Stores",
            sellerVerified: true,
        },
    ];

    const formatCurrency = (amount: number) => {
        return `‚Ç¶${amount.toLocaleString("en-NG", {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2,
        })}`;
    };

    return (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50 overflow-y-auto">
            <div className="bg-white rounded-2xl w-full max-w-md my-8 relative">
                {/* Header */}
                <div className="flex items-center justify-between p-6 border-b border-gray-200">
                    <h2 className="text-xl font-semibold text-gray-900">Order Receipt</h2>
                    <button
                        onClick={onClose}
                        className="text-gray-400 hover:text-gray-600 transition-colors"
                    >
                        <X className="w-5 h-5" />
                    </button>
                </div>

                {/* Content */}
                <div className="p-6 space-y-6">
                    {/* Logo and Title */}
                    <div className="flex items-center justify-between">
                        <div className="flex items-center gap-2">
                            <div className="w-10 h-10 bg-yellow-400 rounded flex items-center justify-center">
                                <span className="text-xs font-bold text-gray-900">üè†</span>
                            </div>
                            <span className="font-bold text-sm">FLEZTA</span>
                        </div>
                        <span className="text-sm text-gray-600">Order Receipt</span>
                    </div>

                    {/* Order Summary */}
                    <div>
                        <h3 className="font-semibold text-gray-900 mb-3">Order summary</h3>
                        <div className="space-y-2 text-sm">
                            <div className="flex justify-between">
                                <span className="text-gray-600">Order ID</span>
                                <span className="text-gray-900 font-medium">
                                    {orderData.orderId}
                                </span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Order time</span>
                                <span className="text-gray-900 font-medium">
                                    {orderData.orderTime}
                                </span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Item(s) total</span>
                                <span className="text-gray-900 font-medium">
                                    {formatCurrency(orderData.itemsTotal)}
                                </span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Shipping</span>
                                <span className="text-gray-900 font-medium">
                                    {formatCurrency(orderData.shipping)}
                                </span>
                            </div>
                            <div className="flex justify-between pt-2 border-t border-gray-200">
                                <span className="text-gray-900 font-semibold">Order total</span>
                                <span className="text-gray-900 font-semibold">
                                    {formatCurrency(orderData.orderTotal)}
                                </span>
                            </div>
                        </div>
                    </div>

                    {/* Shipping Address */}
                    <div>
                        <h3 className="font-semibold text-gray-900 mb-3">
                            Shipping address
                        </h3>
                        <div className="text-sm text-gray-900 space-y-1">
                            <p>{orderData.shippingAddress.name}</p>
                            <p>{orderData.shippingAddress.phone}</p>
                            <p className="whitespace-pre-line">
                                {orderData.shippingAddress.address}
                            </p>
                        </div>
                    </div>

                    {/* Payment Method */}
                    <div>
                        <h3 className="font-semibold text-gray-900 mb-2">Payment method</h3>
                        <div className="bg-green-50 border border-green-200 rounded-lg p-3 mb-3">
                            <p className="text-xs text-green-700 font-medium mb-1">
                                FLEZTA is committed to protecting your payment information
                            </p>
                            <p className="text-xs text-green-700">
                                We use strong encryption and perform regular reviews of it's
                                system to protect your privacy
                            </p>
                        </div>
                        <div className="flex items-center justify-between">
                            <div className="flex items-center gap-3">
                                <div className="flex gap-1">
                                    <div className="w-6 h-4 bg-red-500 rounded-sm"></div>
                                    <div className="w-6 h-4 bg-orange-500 rounded-sm"></div>
                                </div>
                                <div>
                                    <p className="text-sm font-medium text-gray-900">
                                        {orderData.paymentMethod.type} ‚Ä¢{" "}
                                        {orderData.paymentMethod.last4}
                                    </p>
                                    <p className="text-xs text-gray-500">
                                        Paid on {orderData.paymentMethod.paidOn}
                                    </p>
                                </div>
                            </div>
                            <span className="text-sm font-semibold text-gray-900">
                                {formatCurrency(orderData.paymentMethod.amount)}
                            </span>
                        </div>
                    </div>

                    {/* Item Details */}
                    <div>
                        <h3 className="font-semibold text-gray-900 mb-3">
                            Item details ({items.length})
                        </h3>
                        <div className="space-y-3">
                            {items.map((item) => (
                                <div key={item.id} className="flex gap-3">
                                    <div className="w-16 h-16 bg-gray-200 rounded-lg flex-shrink-0"></div>
                                    <div className="flex-1">
                                        <p className="text-sm font-medium text-gray-900 mb-1">
                                            {item.name}
                                        </p>
                                        <div className="flex items-center gap-1 text-xs text-gray-600">
                                            <span>By:</span>
                                            <div className="w-4 h-4 bg-purple-200 rounded-full"></div>
                                            <span>{item.seller}</span>
                                            {item.sellerVerified && (
                                                <span className="text-blue-500">‚úì</span>
                                            )}
                                        </div>
                                    </div>
                                    <div className="text-right">
                                        <p className="text-sm font-semibold text-gray-900">
                                            {formatCurrency(item.price)}
                                        </p>
                                        <p className="text-xs text-gray-500">x{item.quantity}</p>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>

                    {/* Environmental Notice */}
                    <div className="bg-gray-100 rounded-lg p-4">
                        <p className="text-xs text-gray-700">
                            To prevent excess packaging waste, we do not include paper
                            receipts. However, you can always print one out for your records.
                        </p>
                    </div>

                    {/* Action Buttons */}
                    <div className="flex gap-3 pt-2">
                        <button className="flex-1 bg-emerald-900 hover:bg-emerald-950 text-white py-3 rounded-lg font-medium transition-colors">
                            Download Receipt
                        </button>
                        <button className="flex-1 border-2 border-orange-500 text-orange-500 hover:bg-orange-50 py-3 rounded-lg font-medium transition-colors">
                            Share Receipt
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default OrderReceiptModal;